/* Layout */
.container{
    max-width: 1328px; /* 1296 + 32(padding) */
    margin: 0 auto;
    padding-right: 16px;
    padding-left: 16px;
}
@media(max-width:992px){
    .container{
        max-width: 696px;
    }
}
@media(max-width:767px){
    .container{
        max-width: 348px;
    }
}
header, footer{
    background-color: #AA0601;
}
header, header a, footer, footer a{
    color: #FFFFFF;
}
.logo a{
    display: block;
    width: 80px;
    height: 48px;
    background-image: url(../images/logo-white.png);
    background-size: contain;
    background-repeat: no-repeat;
    text-indent: 101%;
    overflow: hidden;
    white-space: nowrap;
}
header ul li a{
    display: block;
    padding-top: 25px;
    padding-bottom: 20px;
    border-bottom: 5px solid #AA0601;
}
header ul li a:hover{
    border-bottom: 5px solid #FFFFFF;
}
@media(max-width:767px){
    .header{
        display: block;
        max-width: 100vw;
        padding-right: 0px;
        padding-left: 0;
    }
    .logo{
        padding-top: 16px;
        padding-bottom: 16px;
    }
    .logo a{
        width: 100%;
        background-position: center;
    }
    .header ul{
        flex-wrap: wrap;
    }
    .header ul li{
        width: 50%;
        margin-right: 0;
        text-align: center;
        border-top: 1px solid #FFFFFF;
    }
    .header ul li:nth-child(1){
        border-right: 1px solid #FFFFFF;
    }
    .header ul li:nth-child(3){
        border-right: 1px solid #FFFFFF;
    }
    .header ul li a{
        padding-top: 12px;
        padding-bottom: 7px;
    }
}
footer .menu-list a{
    display: block;
    border-bottom: 2px solid #AA0601;
}
footer .menu-list a:hover{
    border-bottom: 2px solid #FFFFFF;
}
footer .link-list a{
    display: block;
    width: 48px;
    height: 48px;
}
footer .link-list a:hover{
    width: 50px;
}
footer .link-list-sm a{
    display: block;
    width: 24px;
    height: 24px;
}
footer .link-list-sm{
    display: none;
}
.link-list .facebook, .link-list-sm .facebook{
    background-image: url(../images/ic-social-fb.png);
    background-size: cover;
}
.link-list .instagram, .link-list-sm .instagram{
    background-image: url(../images/ic_social_ig.png);
    background-size: cover;
}
.link-list .line, .link-list-sm .line{
    background-image: url(../images/ic_social_line.png);
    background-size: cover;
}
.material-symbols-outlined{
    font-size: 36px;
    vertical-align: middle;
}
.copyright{
    border-top: 1px solid #FFFFFF;
}
@media(max-width:992px){
    footer .link-list li{
        margin-right: 16px;
    }
    footer .link-list a{
        width: 40px;
        height: 40px;
    }
    footer .link-list a:hover{
        width: 40px;
    }
}
@media(max-width:767px){
    .display-none-sm {
        display: none;
    }
    .sm-layout{
        display: flex;
        justify-content: space-between;
    }
    footer .link-list-sm{
        display: flex;
        height: 100%;
    }
    .contact-sm{
        padding-bottom: 24px;
    }
    .contact-sm li{
        font-size: 16px;
    }
    .material-symbols-outlined{
        font-size: 18px;
    }
    .sm-layout{
        padding-top: 24px;
    }
    .copyright{
        display: block;
        padding-top: 16px;
    }
    .copyright ul{
        display: block;
    }
    .copyright ul li{
        padding-top: 8px;
        font-size: 16px;
    }
    .footer-sm{
        padding-bottom: 24px;
    }
}